import React from "react";
import DatePicker from "react-datepicker";
import TimePicker from "react-time-picker";
import "react-datepicker/dist/react-datepicker.css";
import useHabits from "./useHabits"; // useHabits   

const HabitTrackerSetting = () => {
  const {
    selectedDate,
    setSelectedDate,
    startTime,
    setStartTime,
    endTime,
    setEndTime,
    selectedDays,
    setSelectedDays,
    title,
    setTitle,
    description,
    setDescription,
    habits,
    setHabits,
    selectedHabit,
    setSelectedHabit,
    editing,
    setEditing,
    showForm,
    setShowForm,
    editingTitle,
    setEditingTitle,
    editingPeriod,
    setEditingPeriod,
    editingDescription,
    setEditingDescription,
    editingStartTime,
    setEditingStartTime,
    editingEndTime,
    setEditingEndTime,
    days,
    handleClick,
    validateInputs,
    saveHabit,
    handleHabitClick,
    handleEdit,
    handleDelete,
  } = useHabits(); // useHabits   

  return (
    <>
      <h1> </h1>
      <button onClick={() => setShowForm(!showForm)}></button>
      {showForm && (
        <div className="HabitAdd">
          <DatePicker
            onChange={(date) => {
              setSelectedDate(date);
            }}
            selected={selectedDate}
          />
          <div className="Period">
            <h3> </h3>
            <div>
              {days.map((day) => (
                <span
                  key={day.day}
                  onClick={() => handleClick(day)}
                  style={{
                    fontWeight: selectedDays.some((d) => d.day === day.day)
                      ? "bold"
                      : "normal",
                    margin: "0 5px",
                    cursor: "pointer",
                  }}
                >
                  {day.day}
                </span>
              ))}
              <div>
                 :{" "}
                {selectedDays.length > 0
                  ? selectedDays.map((d) => d.day).join(", ")
                  : ""}
              </div>
            </div>
          </div>
          <div className="Name">
            <h3></h3>
            <input
              placeholder="  "
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            ></input>
          </div>
          <br />
          <span>---  ---</span>
          <div className="Description">
            <h3> </h3>
            <input
              placeholder="   "
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            ></input>
          </div>
          <div className="TimeSelect">
            <h3>  /  </h3>
            <TimePicker onChange={setStartTime} value={startTime} />
            {"  "}
            <TimePicker onChange={setEndTime} value={endTime} />
          </div>
          <br />
          <button onClick={saveHabit}></button>
        </div>
      )}

      {habits.map((habit) => (
        <button key={habit.id} onClick={() => handleHabitClick(habit.id)}>
          {habit.title}
        </button>
      ))}
      {selectedHabit && (
        <div className="HabitDetails">
          {!editing ? (
            <>
              <h2>{selectedHabit.title}</h2>
              <h4>
                 : {new Date(selectedHabit.date).toLocaleDateString()}
              </h4>
              <h4>: {selectedHabit.days.map((d) => d.day).join(", ")}</h4>
              <h4> : {selectedHabit.description}</h4>
              <h4>
                 : {selectedHabit.startTime} /  :{" "}
                {selectedHabit.endTime}
              </h4>
              <button onClick={() => setEditing(true)}> </button>
              <button onClick={() => handleDelete(selectedHabit.id)}>
                 
              </button>
            </>
          ) : (
            <>
              <h2>
                <label>  : </label>
                <input
                  placeholder="  "
                  value={editingTitle}
                  onChange={(e) => setEditingTitle(e.target.value)}
                ></input>
              </h2>
              <h4>
                <label> : </label>
                {days.map((day) => (
                  <span
                    key={day.day}
                    onClick={() => handleClick(day)}
                    style={{
                      fontWeight: selectedDays.some((d) => d.day === day.day)
                        ? "bold"
                        : "normal",
                      margin: "0 5px",
                      cursor: "pointer",
                    }}
                  >
                    {day.day}
                  </span>
                ))}
                <div>
                   :{" "}
                  {selectedDays.length > 0
                    ? selectedDays.map((d) => d.day).join(", ")
                    : ""}
                </div>
              </h4>
              <h4>
                 :{" "}
                <input
                  placeholder="  "
                  value={editingDescription}
                  onChange={(e) => setEditingDescription(e.target.value)}
                ></input>
              </h4>
              <div className="TimeSelect">
                <h3>  /  </h3>
                <TimePicker
                  onChange={setEditingStartTime}
                  value={editingStartTime}
                />
                {"  "}
                <TimePicker
                  onChange={setEditingEndTime}
                  value={editingEndTime}
                />
              </div>
              <button onClick={() => handleEdit(selectedHabit.id)}>
                 
              </button>
            </>
          )}
        </div>
      )}
    </>
  );
};

export default HabitTrackerSetting;
