import React, { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { getFromLocalStorageWithRange } from "../storage/storageHelpers.js";

function WeekPlan() {
  const [date, setDate] = useState(new Date());
  const [weekNum, setWeekNum] = useState(0);
  const onSelectDate = (date) => {
    setDate(date);
  };
  function getWeekInfo(date) {
    //    ( ) ( ) 
    const start = new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate() - date.getDay()
    );
    const end = new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate() + (6 - date.getDay())
    );
    //
    console.log(
      getFromLocalStorageWithRange(
        start.toLocaleDateString(),
        end.toLocaleDateString()
      )
    );
    //
    return {
      start: start.toLocaleDateString(),
      end: end.toLocaleDateString(),
    };
  }

  return (
    <>
      <div>
        <h3>Week selector /  </h3>

        <DatePicker
          dateFormat="yyyy.MM.dd" //  
          shouldCloseOnSelect //   datepicker  
          minDate={new Date("2000-01-01")} // minDate    
          //maxDate={new Date()} // maxDate    
          todayButton="On Today" // today 
          selected={date}
          onChange={(date) => setDate(date)}
        />

        <p>
          <p>Selected date: {date.toLocaleDateString()}</p>
          <p>
            <span>{getWeekInfo(date).start}</span>
            {"~"}
            <span>{getWeekInfo(date).end}</span>
          </p>
        </p>
        <div>        </div>
      </div>
    </>
  );
}

export default WeekPlan;
