import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import TimePicker from "react-time-picker";
import "react-datepicker/dist/react-datepicker.css";

// useHabits   
const useHabits = () => {
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [startTime, setStartTime] = useState("00:00");
  const [endTime, setEndTime] = useState("23:59");
  const [selectedDays, setSelectedDays] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [habits, setHabits] = useState([]);
  const [selectedHabit, setSelectedHabit] = useState(null);
  const [editing, setEditing] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [editingTitle, setEditingTitle] = useState("");
  const [editingPeriod, setEditingPeriod] = useState("");
  const [editingDescription, setEditingDescription] = useState("");
  const [editingStartTime, setEditingStartTime] = useState("00:00");
  const [editingEndTime, setEditingEndTime] = useState("23:59");

  const days = [
    { day: "", order: 1 },
    { day: "", order: 2 },
    { day: "", order: 3 },
    { day: "", order: 4 },
    { day: "", order: 5 },
    { day: "", order: 6 },
    { day: "", order: 7 },
  ];

  useEffect(() => {
    const storedHabits = JSON.parse(localStorage.getItem("habits"));

    if (storedHabits) {
      const parsedHabits = storedHabits.map((habit) => {
        return {
          ...habit,
          date: new Date(habit.date),
        };
      });
      setHabits(parsedHabits);
    }
  }, []);

  const handleClick = (day) => {
    if (selectedDays.some((d) => d.day === day.day)) {
      setSelectedDays(
        selectedDays
          .filter((d) => d.day !== day.day)
          .sort((a, b) => a.order - b.order)
      );
    } else {
      setSelectedDays([...selectedDays, day].sort((a, b) => a.order - b.order));
    }
  };

  const validateInputs = () => {
    if (selectedDays.length === 0 || title.trim() === "") {
      alert("   ");
      return false;
    }

    const startHours = parseInt(startTime.split(":")[0]);
    const startMinutes = parseInt(startTime.split(":")[1]);
    const endHours = parseInt(endTime.split(":")[0]);
    const endMinutes = parseInt(endTime.split(":")[1]);

    if (
      startHours > endHours ||
      (startHours === endHours && startMinutes >= endMinutes)
    ) {
      alert("     ");
      return false;
    }

    return true;
  };

  const saveHabit = () => {
    if (!validateInputs()) return;

    const newHabit = {
      id: Date.now(),
      date: selectedDate.toISOString(),
      startTime,
      endTime,
      days: selectedDays,
      title,
      description,
    };

    setHabits([...habits, newHabit]);
    localStorage.setItem("habits", JSON.stringify([...habits, newHabit]));
  };

  const handleHabitClick = (habitId) => {
    if (selectedHabit && selectedHabit.id === habitId) {
      setSelectedHabit(null);
    } else {
      const habit = habits.find((h) => h.id === habitId);
      setSelectedHabit(habit);
      //console.log(habit);
    }
  };

  const handleEdit = (habitId) => {
    const habitIndex = habits.findIndex((h) => h.id === habitId);
    const updatedHabits = [...habits];
    updatedHabits[habitIndex] = {
      ...selectedHabit,
      date: selectedDate.toISOString(),
      startTime: editingStartTime,
      endTime: editingEndTime,
      days: selectedDays,
      title: editingTitle,
      description: editingDescription,
    };

    setHabits(updatedHabits);
    localStorage.setItem("habits", JSON.stringify(updatedHabits));
    setEditing(false);
  };

  const handleDelete = (habitId) => {
    const updatedHabits = habits.filter((habit) => habit.id !== habitId);
    setHabits(updatedHabits);
    localStorage.setItem("habits", JSON.stringify(updatedHabits));
    setSelectedHabit(null);
  };

  // useHabits   
  return {
    selectedDate,
    setSelectedDate,
    startTime,
    setStartTime,
    endTime,
    setEndTime,
    selectedDays,
    setSelectedDays,
    title,
    setTitle,
    description,
    setDescription,
    habits,
    setHabits,
    selectedHabit,
    setSelectedHabit,
    editing,
    setEditing,
    showForm,
    setShowForm,
    editingTitle,
    setEditingTitle,
    editingPeriod,
    setEditingPeriod,
    editingDescription,
    setEditingDescription,
    editingStartTime,
    setEditingStartTime,
    editingEndTime,
    setEditingEndTime,
    days,
    handleClick,
    validateInputs,
    saveHabit,
    handleHabitClick,
    handleEdit,
    handleDelete,
  };
};

export default useHabits;
