import React, { useState, useEffect } from "react";
import useHabits from "./useHabits"; // useHabits   
import { getTest } from "./getTest";

function HabitTracker() {
  const HabitTrackerSetting = () => {
    const {
      selectedDate,
      setSelectedDate,
      startTime,
      setStartTime,
      endTime,
      setEndTime,
      selectedDays,
      setSelectedDays,
      title,
      setTitle,
      description,
      setDescription,
      habits,
      setHabits,
      selectedHabit,
      setSelectedHabit,
      editing,
      setEditing,
      showForm,
      setShowForm,
      editingTitle,
      setEditingTitle,
      editingPeriod,
      setEditingPeriod,
      editingDescription,
      setEditingDescription,
      editingStartTime,
      setEditingStartTime,
      editingEndTime,
      setEditingEndTime,
      days,
      handleClick,
      validateInputs,
      saveHabit,
      handleHabitClick,
      handleEdit,
      handleDelete,
    } = useHabits(); // useHabits   
    const [testData, setTestData] = useState(null);
    const [checkHabit, setCheckHabit] = useState(false);

    useEffect(() => {
      async function fetchData() {
        const test = "testProgress"; //  test  .
        const result = await getTest(test);
        setTestData(result);
      }

      fetchData();
    }, []);

    if (!testData) {
      return <div>Loading...</div>;
    }

    let today = new Date();
    let year = today.getFullYear(); // 
    let month = today.getMonth() + 1; // 
    let date = today.getDate(); // 
    let day = today.getDay(); // 

    const habitCheckClicked = (event) => {
      setCheckHabit(event.target.checked);
      if (event.target.checked === true) {
        localStorage.setItem(selectedHabit.title, "true");
      } else {
        localStorage.setItem(selectedHabit.title, "false");
      }
    };

    return (
      <>
        <p>
          <ol>
            <li>
                    ,  
                 ,   .
            </li>
            <li>
                  , /   ,
               .(  ,  
                .      )  /
                   .      
              .
            </li>
          </ol>
        </p>

        <h2>Today : {year + "/" + month + "/" + date}</h2>
        <ul>
          <label>
            <h3>You did</h3>
          </label>
          <li>1</li>
          <li>2</li>
        </ul>
        <h2>Habit List</h2>
        {habits.map((habit) => (
          <button key={habit.id} onClick={() => handleHabitClick(habit.id)}>
            {habit.title}
          </button>
        ))}
        {selectedHabit != null ? (
          <>
            <div>
              <h3>  : {selectedHabit.title}</h3>
              <input type="checkbox" onChange={habitCheckClicked} />
            </div>
          </>
        ) : (
          <div></div>
        )}
        {JSON.stringify(testData)}
      </>
    );
  };

  return <HabitTrackerSetting />;
}

export default HabitTracker;
